<template>
    <div class="vh-100 d-flex justify-content-center align-items-center">
        <canvas id="background-canvas"></canvas>
      <div class="flip-clock" v-if="!showLoginForm">
        <div class="flip-clock__piece">
            <span class="flip-clock__slot">
                <div class="time-component" v-for="time in countdown" :key="time.label">
                    <div class="label">{{ time.label }}</div>
                    <div class="value">{{ time.value }}</div>
                </div>
            </span>
        </div>
      </div>
      <div v-else>
        <div class="login-form-container text-center">
            <b-form @submit.prevent="checkPasscode">
                <b-form-group class="input-group-append" id="input-group-1" label="" label-for="input-1">
                    <b-form-input
                        id="input-1"
                        v-model="passcode"
                        type="password"
                        required
                        placeholder="Enter passcode"
                        pattern="[0-9]*"
                        inputmode="numeric"
                    ></b-form-input>
                </b-form-group>
                <div class="mb-2"></div> 
                <b-button type="submit" class="neon-button">Enter</b-button>
            </b-form>
            <b-alert v-if="showAlert" variant="danger" dismissible>
                Wrong passcode. Try again!
            </b-alert>
        </div>
      </div>
    </div>
  </template>
  
<script>
export default {
    name: 'LoginPage',
    data() {
        return {
            passcode: '',
            showAlert: false,
            countdownDate: new Date("2023-05-17T00:00:00").getTime(), // Set your target date here
            now: new Date().getTime(),
            interval: null,
        }
    },
    computed: {
        countdown() {
            const distance = this.countdownDate - this.now;

            const days = Math.floor(distance / (1000 * 60 * 60 * 24));
            const hours = Math.floor((distance % (1000 * 60 * 60 * 24)) / (1000 * 60 * 60));
            const minutes = Math.floor((distance % (1000 * 60 * 60)) / (1000 * 60));
            const seconds = Math.floor((distance % (1000 * 60)) / 1000);

            return [
                { label: 'Days', value: days },
                { label: 'Hours', value: hours },
                { label: 'Minutes', value: minutes },
                { label: 'Seconds', value: seconds },
            ];
        },
        showLoginForm() {
            return this.now >= this.countdownDate;
        },
    },
    created() {
        this.interval = setInterval(() => {
            this.now = new Date().getTime();
        }, 1000);
    },
    beforeDestroy() {
        clearInterval(this.interval);
    },
    methods: {
        checkPasscode() {
            if (this.passcode === "20230226") {  // replace "1234" with your desired passcode
                this.$router.push({ name: 'HomePage' });
            } else {
                this.showAlert = true;
            }
        }
    },
    mounted() {
        const canvas = document.getElementById('background-canvas');
        const ctx = canvas.getContext('2d');
        
        // Set canvas dimensions to match the container size
        canvas.width = window.innerWidth;
        canvas.height = window.innerHeight;
        
        // Set the background color
        ctx.fillStyle = 'rgba(134, 189, 255, 0.35)'; // Replace with desired background color
        ctx.fillRect(0, 0, canvas.width, canvas.height);
    },
}
</script>

